.containerTag{
	display: inline-block;
	position: -webkit-sticky;
	position: sticky;
	margin-top: -20px;
	top:30px;
	text-align: center;
	-webkit-transition:  0.3s ease-in-out 0s;
	-o-transition:  0.3s ease-in-out 0s;
	transition:  0.3s ease-in-out 0s;
}
.containerAddHistory{
	display: inline-block;
	position: -webkit-sticky;
	position: sticky;
	margin-top: -30px;
	margin-bottom: -60px;
	top:30px;
	text-align: center;
	-webkit-transition:  0.3s ease-in-out 0s;
	-o-transition:  0.3s ease-in-out 0s;
	transition:  0.3s ease-in-out 0s;
}
/**
*IMAGES LOAD:
*/

.signPostLeft{
	background-image: url('../../images/signpostleft.png');	
	width: 173px;
	height: 113px;
	z-index: 3;
}
.signPostCenter{
	background-image: url('../../images/hanging_poster.png');	
	width: 165px;
	height: 113px;
	z-index: 2;
}
.signPostRight{
	background-image: url('../../images/signpostright.png');	
	width: 173px;
	height: 113px;
	z-index: 1;
}
.addHistory{
	background-image: url('../../images/hanging_poster.png');	
	width: 165px;
	height: 113px;
	z-index: 1;
}

/**
*TEXT POSITIONS:
*/
.textTag{
	position: absolute;
	font-size: 24px;
	font-weight: bold;
	color:black;
}
.iconAddHistory{
	position: absolute;
	font-size: 28px;
	font-weight: bold;
	color:black;
	top: 50%;
    left: 50%;
    -webkit-transform: translate(20%, 220%);
        -ms-transform: translate(20%, 220%);
            transform: translate(20%, 220%);
}
.iconCamHistory{
	position: absolute;
	font-size: 38px;
	font-weight: bold;
	color:black;
	top: 50%;
    left: 50%;
    -webkit-transform: translate(-120%, 150%);
        -ms-transform: translate(-120%, 150%);
            transform: translate(-120%, 150%);
}

.textTagCenter{
	top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, 5%);
        -ms-transform: translate(-50%, 5%);
            transform: translate(-50%, 5%);
}


.textTagRight{
	top: 50%;
    left: 50%;
    -webkit-transform: translate(-45%, -5%) rotate(-9deg);
        -ms-transform: translate(-45%, -5%) rotate(-9deg);
            transform: translate(-45%, -5%) rotate(-9deg);
}

.textTagLeft{
	top: 50%;
    left: 50%;
    -webkit-transform: translate(-55%, -5%) rotate(9deg);
        -ms-transform: translate(-55%, -5%) rotate(9deg);
            transform: translate(-55%, -5%) rotate(9deg);
}

/**
*TRANSITIONS:
*/

.translateDown:hover{
	-webkit-transform: translate(0px, 20px);
	    -ms-transform: translate(0px, 20px);
	        transform: translate(0px, 20px);
}

.rotateLeft:hover{
	-webkit-transform: rotate(9deg);
	    -ms-transform: rotate(9deg);
	        transform: rotate(9deg);
}
.rotateRight:hover{
	-webkit-transform: rotate(-9deg);
	    -ms-transform: rotate(-9deg);
	        transform: rotate(-9deg);
}



